/**
 * 
 * 
 */

import type { PostgrestError } from '@supabase/supabase-js';

/**
 * 
 */
export type ErrorCode =
  | 'UNAUTHORIZED'
  | 'FORBIDDEN'
  | 'NOT_FOUND'
  | 'VALIDATION_ERROR'
  | 'DATABASE_ERROR'
  | 'INTERNAL_ERROR';

/**
 * 
 */
export interface AppError {
  code: ErrorCode;
  message: string;
  status: number;
  cause?: unknown;
}

/**
 * 
 */
const ERROR_STATUS_MAP: Record<ErrorCode, number> = {
  UNAUTHORIZED: 401,
  FORBIDDEN: 403,
  NOT_FOUND: 404,
  VALIDATION_ERROR: 400,
  DATABASE_ERROR: 500,
  INTERNAL_ERROR: 500,
};

/**
 * Supabase
 * @param error - Supabase
 * @returns 
 */
export function handleSupabaseError(error: PostgrestError): AppError {
  // 
  let code: ErrorCode = 'DATABASE_ERROR';
  if (error.code === 'PGRST116') {
    code = 'NOT_FOUND';
  } else if (error.code === '42501') {
    code = 'FORBIDDEN';
  } else if (error.code === '23505') {
    code = 'VALIDATION_ERROR';
  }

  return {
    code,
    message: error.message,
    status: ERROR_STATUS_MAP[code],
    cause: error,
  };
}

/**
 * 
 * @param error - 
 * @returns 
 */
export function handleUnknownError(error: unknown): AppError {
  if (error instanceof Error) {
    return {
      code: 'INTERNAL_ERROR',
      message: error.message,
      status: 500,
      cause: error,
    };
  }

  return {
    code: 'INTERNAL_ERROR',
    message: '',
    status: 500,
    cause: error,
  };
}

/**
 * 
 * @param error - 
 * @returns 
 */
export function handleError(error: unknown): AppError {
  if ((error as PostgrestError).code !== undefined) {
    return handleSupabaseError(error as PostgrestError);
  }

  return handleUnknownError(error);
}
